<template>
  <el-form
    class="ling-wifi-info"
    label-position="right"
    label-width="100px"
    :model="wifiInfo"
    ref="wifiForm"
    v-loading="loading">
    <el-form-item label="Wifi名称：">
      <el-input
        v-model="wifiInfo.wifi_name"
        required>
      </el-input>
    </el-form-item>

    <el-form-item label="Wifi密码：">
      <el-input
        v-model="wifiInfo.wifi_passwd"
        required>
      </el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('wifiForm')">保存</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingWifiSetting',
  mixins: [getSettingInfo],
  data () {
    return {
      loading: false,
      wifiInfo: {
        wifi_name: '',
        wifi_passwd: ''
      }
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.wifiInfo.wifi_name = val.wifi_name
        this.wifiInfo.wifi_passwd = val.wifi_passwd
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setWifi: 'setWifi'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            await this.setWifi(this.wifiInfo)
          } catch (res) {}

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-wifi-info {
  .el-form-item {
    .el-input__inner {
      font: {
        size: 16px;
        weight: bold;
      }
    }
  }
}
</style>



